		}
	}

	.select-container {
		width: 100%;
		margin-bottom: 20px;
		border-radius: 100px;
		position: relative;

		&:before {
			content: '';
			background: url('~@/assets/images/arrowhead.svg') no-repeat center;
			background-size: 15px 15px;
			transform: rotate(270deg);
			position: absolute;
			right: 19px;
			top: 10px;
			width: 15px;
