			pointer-events: none;
		}

		select {
			padding: 8px 19px;
			border-radius: 100px;
			border: 1px solid $white;
			width: inherit;
			font-size: 12px;
			color: $black;
			outline: none;
			-webkit-appearance: none;
			-moz-appearance: none;
