<template>
	<div
		data-vue-component-name="FiltersCheckbox"
		:class="{ checked, sub }"
	>
		<div class="checkbox">
			<Checkbox
				@click="onClick"
				:id="filter.value"
				:checked="!!checked"
				:type="checked === 2 ? 'square' : 'check'"
			/>

			<label :for="filter.value">
				{{ filter.name }}
			</label>
		</div>

		<div class="jobs-count">
			<span>{{ filter.count }}</span>
		</div>
	</div>
</template>

<script>
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

import Checkbox from '../ui/Checkbox.vue'

export default {
	components: { Checkbox },

	emits: ['selectFilter'],
	props: { filter: Object, sub: Boolean, checked: Number },

	setup(props, ctx) {
		const store = useStore()
		const route = useRoute()

		function onClick() {
			ctx.emit('selectFilter', props.filter)

			if (route.name === 'PostedJobs')
				store.dispatch('postedJobs/getJobs')
		}

		return { onClick }
	}
}
</script>

