	display: flex;

	&.checked {
		.checkbox label,
		.jobs-count span {
			font-weight: 500;
		}
	}

	&.sub {
		.checkbox label {
			font-size: 10px;
		}

		.jobs-count span {
			font-size: 8px;
			min-width: 22px;
			height: 22px;
			padding: 5px;
		}
	}

	.checkbox {
