<template>
	<div
		data-placeholder="Job title or keywords"
		data-vue-component-name="Search"
	>
		<input
			type="text"
			placeholder="Job title or keywords"
			v-model="jobName"
			@keyup.enter="findJobs"
		/>

		<Button
			v-if="btnTitle"
			@click="findJobs"
		>
			{{ btnTitle }}
		</Button>
	</div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'
import scrollTo from '@/helpers/scrollTo'

import Button from '../ui/Button.vue'

export default {
	components: { Button },

	emits: ['onClickHandler'],
	props: {
		btnTitle: {
			type: String,
			default: 'Search'
		}
	},

	setup(props, ctx) {
		const store = useStore()

		const jobName = computed({
			get: () => store.state.filters.searchStr,
			set: val => store.commit('filters/setSearchStr', val)
		})

		function findJobs() {
			ctx.emit(
				'onClickHandler',
				() => scrollTo('jobs')
			)
		}

		return { jobName, findJobs }
	}
}
</script>

