	padding: 40px 0;
	display: flex;
	flex-direction: column;
	justify-content: space-between;

	.additional-buttons {
		display: flex;
		flex-direction: column;
		margin-bottom: 30px;
		justify-content: center;
		max-width: 350px;

		[data-vue-component-name="Button"] {
			margin-bottom: 15px;

			@include media('<=desktop') {
				margin-right: 0;
				margin-bottom: 15px;
			}
		}

		@include media('<=desktop') {
			flex-direction: column;
		}
	}

	.back {
		font-size: 12px;
		font-weight: 500;
	}

	@include media('<=desktop') {
		[data-vue-component-name="JobInfo"] {
			padding-top: 0;
		}
	}
}
