<template>
	<div data-vue-component-name="Profile">
		<GreyBlank title="My Profile">
			<div class="profile-container">
				<div class="profile-item">
					<span>Full name</span>
					<strong>{{ userFullName }}</strong>
				</div>

				<div class="profile-item last">
					<span>Email</span>
					<strong>{{ userEmail }}</strong>
				</div>

				<Button
					@click="$router.push({ name: 'DashboardUser' })"
					black
					full
				>
					Edit profile
				</Button>
			</div>
		</GreyBlank>
	</div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'

import Button from '@/components/ui/Button.vue'
import GreyBlank from './GreyBlank.vue'

export default {
	components: { GreyBlank, Button },

	setup() {
		const store = useStore()

		const userFullName = computed(
			() => store.getters['user/userFullName']
		)

		const userEmail = computed(
			() => store.getters['user/userEmail']
		)

		return { userFullName, userEmail }
	}
}
</script>

