<template>
	<div data-vue-component-name="JobInfo">
		<p class="description">
			You are applying for the following job:
		</p>

		<h1 class="job-name">
			{{ appliedJob.title }}
		</h1>

		<div class="job-info">
			<span class="city">
				{{ formattedJobTitle(appliedJob.city, appliedJob.state) }}
			</span>
			<span class="date">
				Posted on
				{{ appliedJob.date && formattedDate(appliedJob.date) }}
			</span>
		</div>
	</div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'

import formattedJobTitle from '@/helpers/formattedJobTitle'

export default {
	setup() {
		const store = useStore()

		const appliedJob = computed(
			() => store.state.postedJobs.job || {}
		)

		function formattedDate(dateStr) {
			const date = new Date(dateStr)

			const day = date.getDate()
			const year = date.getFullYear()
			const month = date.toLocaleString('default', { month: 'long' })

			return `${month} ${day}, ${year}`
		}

		return { appliedJob, formattedDate, formattedJobTitle }
	}
}
</script>

