<template>
	<div data-vue-component-name="User">
		<GreyBlank>
			<div class="user-container">
				<h1>{{ userFullName }}</h1>

				<Button
					v-if="$route.name === 'Dashboard'"
					@click="$router.push({ name: 'DashboardUser' })"
					black
					full
				>
					Edit profile
				</Button>

				<Link v-else :to="{ name: 'ResetPassword' }">
					Reset Password
				</Link>

				<Link v-if="!rtbfRequested" @click="requestRtbf">
					Request RTBF
				</Link>

				<p v-else>Your RTBF request has been issued</p>
			</div>
		</GreyBlank>
	</div>
</template>

<script>
import { computed } from 'vue'
import { useStore } from 'vuex'

import Link from '@/components/ui/Link.vue'
import Button from '@/components/ui/Button.vue'
import GreyBlank from './GreyBlank.vue'

export default {
	components: { Link, Button, GreyBlank },

	setup() {
		const store = useStore()

		const userFullName = computed(
			() => store.getters['user/userFullName']
		)

		const rtbfRequested = computed(
			() => store.state.user.rtbfRequested
		)

		async function requestRtbf() {
			await store.dispatch('user/requestRtbf')
		}

		return { userFullName, rtbfRequested, requestRtbf }
	}
}
</script>

