<template>
	<Wrapper v-if="!isLoading">
		<Image
			class="left-slot"
			imgName="sign-in.jpg"
			figureColor="#b1ffe0"
		/>

		<SignInApply class="right-slot" />
	</Wrapper>
</template>

<script>
import { onMounted, computed } from 'vue'
import { useStore } from 'vuex'
import { useRoute } from 'vue-router'

import Wrapper from '@/components/entry/Wrapper.vue'
import Image from '@/components/entry/Image.vue'
import SignInApply from '@/components/entry/form/SignInApply.vue'

export default {
	components: { Wrapper, Image, SignInApply },

	setup() {
		const store = useStore()
		const route = useRoute()

		const isLoading = computed(
			() => store.state.isPageLoading
		)

		onMounted(
			async () => {
				if (route.params.id)
					await store.dispatch('postedJobs/getJob', +route.params.id)

				store.commit('setIsPageLoading', false)
			}
		)

		return { isLoading }
	}
}
</script>
