<template>
	<div data-vue-component-name="SignInApply">
		<h1 class="title">
			Sign in and apply
		</h1>

		<div>
			<UserSignOut />

			<form
				v-if="!isLoggedIn"
				@submit.prevent="onSubmit"
			>
				<Input
					v-if="!userEmail"
					label="Email"
					type="email"
					v-model="email.value.value"
					:error="email.errorMessage.value"
				/>

				<div class="password">
					<Link :to="{ name: 'ResetPassword' }">
						Forgot Password?
					</Link>

					<Input
						label="Password"
						type="password"
						v-model="password.value.value"
						:error="password.errorMessage.value"
					/>
				</div>

				<ButtonLoader :loading="loading">
					Sign in
				</ButtonLoader>
			</form>

			<ChooseResume
				v-else
				message="Choose your resume from the list below or upload a new resume."
			/>
		</div>

		<RegisterLink to="RegisterApply" />
	</div>
</template>

<script>
import { computed, watch } from 'vue'
import { useStore } from 'vuex'
import { useForm, useField } from 'vee-validate'
import { useLoader } from '@/hooks'

import * as v from '@/helpers/validators'
import API_ERROR_CODES from '@/constants/apiErrorCodes'

import Link from '@/components/ui/Link.vue'
import Input from '@/components/ui/Input.vue'
import ButtonLoader from '@/components/ui/ButtonLoader.vue'
import RegisterLink from '../RegisterLink.vue'
import UserSignOut from '../UserSignOut.vue'
import ChooseResume from '../ChooseResume.vue'

export default {
	components: {
		Link,
		Input,
		ButtonLoader,
		RegisterLink,
		UserSignOut,
		ChooseResume
	},

	setup() {
		const store = useStore()
		const [loading, proceedLoading] = useLoader()

		const { handleSubmit, setFieldValue } = useForm()
		const email = useField('email', v.email)
		const password = useField('password', v.password)

		const userEmail = computed(() => store.getters['user/userEmail'])
		const userName = computed(() => store.getters['user/userFullName'])
		const error = computed(() => store.state.error)
		const isLoggedIn = computed(() => store.state.user.isLoggedIn)

		watch(
			userEmail,
			() => setFieldValue('email', userEmail.value),
			{ immediate: true }
		)

		const onSubmit = handleSubmit(
			async (values, actions) => {
				await proceedLoading(
					() => store.dispatch('user/signIn', values)
				)

				if (error.value) {
					const { code } = error.value.response.data
					const handledCode = API_ERROR_CODES[code]

					return handledCode && actions.setFieldError(
						handledCode.field,
						handledCode.message
					)
				}
			}
		)

		return {
			userEmail,
			userName,
			isLoggedIn,
			email,
			password,
			onSubmit,
			loading
		}
	}
}
</script>

