		}
	}

	form {
		margin-bottom: 30px;

		[data-vue-component-name="Input"],
		[data-vue-component-name="InputFile"] {
			margin-bottom: 10px;
		}

		.password {
			position: relative;

			[data-vue-component-name="Link"] {
				position: absolute;
				top: 0;
				right: 0;
				z-index: 1;
			}
		}
	}
}
</style>
